import Api from '@/common/api';
export default {
	data() {
		return {
			params:{
				oldPassword: '',
				newPassword: '',
				confirmPassword:""
			}
		};
	},
	methods: {
		async formSubmit() {
			if(this.params.newPassword!=this.params.confirmPassword ){
				this.$api.msg("密码不一致")
				return;
			}
				const data = await Api.apiCall('POST', Api.member.resetPwd, this.params);
				if(data.code!=0){
					this.$api.msg(data.msg);
					return;
				}
			uni.showToast({
				title: '密码修改成功',
				icon: 'success',
				success() {
					setTimeout(function() {
						uni.navigateTo({
							url:'/pages/public/login'
						});
					}, 1000);
				}
			});
	}
	}
};
